.main_products {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  width: 95vw;
  margin: auto;
  margin-top: 20px;
  margin-bottom: 80px;
}

.section_all_product {
  margin: 10px;
  box-shadow: 0px 0px 7px 0px rgba(166, 166, 166, 0.371);
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 230px;
  position: relative;
  z-index: 250;
}
.section_all_product p {
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  margin: 5px;
  color: rgb(101, 101, 101);
  margin: auto;
  padding: 5px;
}

.section_all_product img {
  width: 130px;
  height: 130px;
  margin: auto;
}
.img_all {
  width: 100vw;
  height: 170px;
}

.price {
  width: max-content;
  position: absolute;
  top: 15px;
  right: 15px;
  padding: 5px;
  border-radius: 30px;
  background-color: #ffbc5c;
  color: rgb(47, 47, 47);
  font-family: "Montserrat", sans-serif;
  font-size: 12px;
  box-shadow: 2px 2px 3px 2px rgba(0, 0, 0, 0.124);
}

.navlink_all {
  text-decoration: none;
  color: black;
}

.container_nav_all {
  position: fixed;
  bottom: 1px;
  height: 60px;
  width: 100vw;
}

.talkbubble {
  width: 85vw;
  height: 60px;
  position: fixed;
  bottom: 15px;
  background-color: rgb(255, 255, 255);
  display: flex;
  justify-content: space-around;
  align-items: center;
  left: 7.5vw;
  box-shadow: 2px 2px 5px 2px rgba(152, 152, 152, 0.355);
  border-radius: 30px;
  z-index: 450;
}


.container_cart_all_product {
  width: 60px;
  height: 60px;
  z-index: 100;
  bottom: 22px;
  border-radius: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #f12711; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #f5af19,
    #f45c4b
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #f5af19, #f45c4b);
  box-shadow: 0px 0px 20px 5px #ff7700;
}
.icon_cart_all_products {
  z-index: 100;
  width: 35px;
  height: 35px;
  color: rgb(70, 70, 70);
}

.container_cart_all_product {
  font-size: 14px;
  font-family: "Montserrat", sans-serif;
  color: rgb(55, 55, 55);
}

.icon_like {
  width: 30px;
  height: 30px;
}
.icon_user {
  width: 30px;
  height: 30px;
  color: grey;
}

.no_like {
  position: absolute;
  top: 15px;
  left: 10px;
  width: 35px;
  height: 35px;
  padding: 3px;
  border-radius: 100%;
  background-color: white;
  box-shadow: 0px 0px 5px 0px rgba(200, 200, 200, 0.524);
  z-index: 331200;
}
